import React, {Component} from 'react';
import {Image, HelpBlock, Form, FormControl, FormGroup, Button, ControlLabel} from 'react-bootstrap';
import './EditInfo.css';
import $ from "jquery";
import {loginID} from "./Toolbar";

function FieldGroup({id, label, help, ...props}) {
    return (
        <FormGroup controlId={id}>
            <ControlLabel>{label}</ControlLabel>
            <FormControl {...props} />
            {help && <HelpBlock>{help}</HelpBlock>}
        </FormGroup>
    );
}

var upload;

class EditInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            header: null,
            cover: null,
        };
        this.getText = this.getText.bind(this);
        this.hdnh = this.hdnh.bind(this);
        this.hdit = this.hdit.bind(this);
        this.submit = this.submit.bind(this);
        this.getText();
    }

    getText() {
        const _this = this;
        $.ajax({
            type: "POST",
            async: true,
            url: "http://localhost:80/UserManager/Info",
            crossDomain: true,
            data: {"userid": loginID},
            dataType: 'text',
            jsonp: "jsonpCallback",
            jsonpCallback: "success_jsonpCallback",
            success: function (data) {
                var json = JSON.parse(data);
                _this.setState({
                    data: json.intro,
                    header: json.head,
                })
                alert(_this.state.header);
            },
            error: function () {
                alert('error');
            }
        });
    }

    hdit(e) {
        this.setState({
            data: e.target.value,
        });
    }

    hdnh(e) {
        upload = e.target.files[0];
        //alert(upload);
        // var head0 = e.target.input.files[0];
        // const _this = this;
        // $.ajax({
        //     type: 'POST',
        //     async: false,
        //     url: "http://localhost:80/UserManager/Head",
        //     crossDomain: true,
        //     data: {
        //         "userid": loginID,
        //         "head": head0,
        //     },
        //     dataType: 'text',
        //     success: function (data) {
        //     },
        //     error: function (xhr, msg) {
        //         alert(msg + '\n' + xhr.responseText);
        //         console.log(msg + '\n' + xhr.responseText);
        //     }
        // });
    }

    submit(e) {
        e.preventDefault();
        const _this = this;
        var formData = new FormData();
        formData.append("userid", loginID);
        formData.append("intro", _this.state.data);
        formData.append("head", upload);
        $.ajax({
            type: 'POST',
            async: false,
            url: "http://localhost:80/UserManager/Info/Update",
            crossDomain: true,
            processData: false,
            contentType : false,
            data: formData,
                //"userid": loginID,
                //"intro": _this.state.data,
                //"head": upload,
                //"head": _this.state.head,
            dataType: 'text',
            success: function (data) {
            },
            error: function (xhr, msg) {
                alert(msg + '\n' + xhr.responseText);
                console.log(msg + '\n' + xhr.responseText);
            }
        });
    }

    render() {
        return (
            <Form id="formei">
                {/*<img src='data:image/jpg;base64,"+strs[3]+" '/>*/}
                <Image src={"data:image/png;base64," + this.state.header} rounded/>
                <FieldGroup
                    id="formControlsFile"
                    type="file"
                    label="Head Picture"
                    help=""
                    value={this.state.head}
                    onChange={this.hdnh}
                />
                <FieldGroup
                    id="formControlsFile"
                    type="file"
                    label="Bookcover Picture"
                    help=""
                />
                <FormGroup controlId="formControlsTextarea">
                    <ControlLabel>Brief Introduction</ControlLabel>
                    <FormControl componentClass="textarea" placeholder="Introduce your favourite book breifly"
                                 value={this.state.data} onChange={this.hdit}/>
                </FormGroup>
                <div id="sm">
                    <Button bsSize="large" id="smb" onClick={this.submit}>
                        Submit
                    </Button>
                </div>
            </Form>
        );
    }
}

export {EditInfo};


