import React, {Component} from 'react';
import "./Users.css";
import {Table, Form, FormControl, FormGroup, Button} from 'react-bootstrap';
import {EE} from './Home';
import $ from "jquery";

const useful = 2;
const del = 2;
const dis = 3;
const act = 4;

class Users extends Component {
    static defaultProps = {
        headers: [
            "UserID", "Power", "Delete", "Disable", "Activate"
        ],
        initialData: [
            ["admin", "0", "", "", ""],
            ["customer", "1", "", "", ""],
        ],
    };

    constructor(props) {
        super(props);
        this.state = {
            rowidx: null,
            btstyle: null,
            data: this.props.initialData,
            sortby: null,
            DESC: false,
            edit: null, //{row: index, cell: index}
            search: false,
            preSearchData: null,
            needles: new Array(useful),
        };
        this.sort = this.sort.bind(this);
        this.showEditor = this.showEditor.bind(this);
        this.save = this.save.bind(this);
        this.renderTable = this.renderTable.bind(this);
        this.renderSearch = this.renderSearch.bind(this);
        this.search = this.search.bind(this);
        this.deleteUser = this.deleteUser.bind(this);
        this.disableUser = this.disableUser.bind(this);
        this.activateUser = this.activateUser.bind(this);
        this.getUsers = this.getUsers.bind(this);
        EE.on('pushSearch', this.toggleSearch.bind(this));
        this.getUsers();
    }

    getUsers() {
        const _this = this;
        $.ajax({
            type: "GET",
            async: true,
            url: "http://localhost:80/UserManager",
            dataType: "jsonp",
            jsonp: "jsonpCallback",
            jsonpCallback: "success_jsonpCallback",
            success: function (data) {
                _this.setState({
                    data: JSON.parse(JSON.stringify(data)),
                });
            },
            error: function () {
                alert('error');
            }
        });
    }

    sort(e) {
        if (e.target.cellIndex < useful) {
            var column = e.target.cellIndex;
            var data = Array.from(this.state.data);
            var DESC = this.state.sortby === column && !this.state.DESC;
            data.sort(function (a, b) {
                return DESC
                    ? (a[column] < b[column] ? 1 : -1)
                    : (a[column] > b[column] ? 1 : -1);
            });
            this.setState({
                data: data,
                sortby: column,
                DESC: DESC,
            });
        }
    }

    showEditor(e) {
        if (e.target.cellIndex < useful)
            this.setState({
                    edit: {
                        row: parseInt(e.target.dataset.row, 10),
                        cell: e.target.cellIndex,
                    }
                }
            )
    }

    save(e) {
        e.preventDefault();
        var input = e.target.firstChild;
        var data = Array.from(this.state.data);
        data[this.state.edit.row][this.state.edit.cell] = input.value;
        this.setState({
            edit: null,
            data: data,
        })
    }

    toggleSearch() {
        if (this.state.search) {
            this.setState({
                data: this.state.preSearchData,
                search: false,
                preSearchData: null,
            });
        }
        else {
            this.setState({
                preSearchData: this.state.data,
                search: true,
            });
        }
    }

    search(e) {
        var needle = e.target.value.toLowerCase();
        var needles = this.state.needles;
        var data = this.state.preSearchData;
        needles[e.target.dataset.idx] = needle;
        var see = function (row) {
            return row[i].toString().toLowerCase().indexOf(needle) > -1;
        };
        for (var i = 0; i < useful; i++) {
            needle = needles[i];
            if (needle) {
                data = data.filter(see);
            }
        }
        this.setState({
            data: data,
            needles: needles,
        });
    }

    deleteUser(e) {
        var data0 = this.state.data;
        var row = e.target.getAttribute('row');
        const _this = this;
        $.ajax({
            type: 'POST',
            async: false,
            url: "http://localhost:80/UserManager/Delete",
            crossDomain: true,
            data: {
                "userid": data0[row][0]
            },
            dataType: 'text',
            success: function (data) {
                data0.splice(row, 1);
                _this.setState({
                    data: data0,
                });
            },
            error: function (xhr, msg) {
                alert(msg + '\n' + xhr.responseText);
                console.log(msg + '\n' + xhr.responseText);
            }
        });
    }

        disableUser(e) {
        var data0 = this.state.data;
        var row = e.target.getAttribute('row');
        const _this = this;
        $.ajax({
            type: 'POST',
            async: false,
            url: "http://localhost:80/UserManager/Disable",
            crossDomain: true,
            data: {
                "userid": data0[row][0]
            },
            dataType: 'text',
            success: function (data) {
                data0[row][2] = "";
                data0[row][3] = "1";
                data0[row][4] = "";
                _this.setState({
                    data: data0,
                });
            },
            error: function (xhr, msg) {
                alert(msg + '\n' + xhr.responseText);
                console.log(msg + '\n' + xhr.responseText);
            }
        });
    }

        activateUser(e) {
        var data0 = this.state.data;
        var row = e.target.getAttribute('row');
        const _this = this;
        $.ajax({
            type: 'POST',
            async: false,
            url: "http://localhost:80/UserManager/Activate",
            crossDomain: true,
            data: {
                "userid": data0[row][0]
            },
            dataType: 'text',
            success: function (data) {
                data0[row][2] = "";
                data0[row][3] = "";
                data0[row][4] = "1";
                _this.setState({
                    data: data0,
                });
            },
            error: function (xhr, msg) {
                alert(msg + '\n' + xhr.responseText);
                console.log(msg + '\n' + xhr.responseText);
            }
        });
    }

    renderTable() {
        return (
            <div id="table">
                <Table striped bordered condensed hover>
                    <thead onClick={this.sort}>
                    <tr>
                        {this.props.headers.map(function (title, idx) {
                            if (this.state.sortby === idx) {
                                title += this.state.DESC ? '\u2191' : '\u2193'
                            }
                            return (<th id="thh" key={idx}>{title}{" "}</th>);
                        }, this)}
                    </tr>
                    </thead>
                    <tbody onDoubleClick={this.showEditor}>
                    {this.renderSearch()}
                    {this.state.data.map(function (row, rowidx) {
                        return (
                            <tr key={rowidx}>{row.map(function (cell, idx) {
                                var content = cell;
                                var edit = this.state.edit;
                                if (idx < useful) {
                                    if (edit && edit.row === rowidx && edit.cell === idx) {
                                        content = <Form onSubmit={this.save}>
                                            <FormControl id="editInput" type="text" defaultValue={content}/> </Form>
                                    }
                                    return (<td key={idx} data-row={rowidx}>{content}</td>);
                                }
                                if (idx === del) {
                                    if (content === '1') {
                                        return (
                                            <td key={idx} data-row={rowidx}>
                                                <Button row={rowidx} bsStyle="danger" bsSize="xsmall" disabled>Delete</Button>
                                            </td>
                                        );
                                    }
                                    return (
                                        <td key={idx} data-row={rowidx}>
                                            <Button row={rowidx} bsStyle="danger" bsSize="xsmall" onClick={this.deleteUser}>Delete</Button>
                                        </td>
                                    );
                                }
                                if (idx === dis) {
                                    if (content === '1') {
                                        return (
                                            <td key={idx} data-row={rowidx}><Button row={rowidx} bsStyle="warning" disabled bsSize="xsmall">Disable</Button></td>
                                        );
                                    }
                                    return (
                                        <td key={idx} data-row={rowidx}><Button row={rowidx} bsStyle="warning" bsSize="xsmall" onClick={this.disableUser}>Disable</Button></td>
                                    );
                                }
                                if (idx === act) {
                                    if (content === '1') {
                                        return (
                                            <td key={idx} data-row={rowidx}><Button row={rowidx} bsStyle="success" bsSize="xsmall" disabled>Activate</Button></td>
                                        );
                                    }
                                    return (
                                        <td key={idx} data-row={rowidx}><Button row={rowidx} bsStyle="success" bsSize="xsmall" onClick={this.activateUser}>Activate</Button></td>
                                    );
                                }
                                return (<td key={idx} data-row={rowidx}></td>);
                            }, this)}</tr>
                        );
                    }, this)}
                    </tbody>
                </Table>
            </div>
        );
    }

    renderSearch() {
        if (!this.state.search) {
            return null;
        }
        return (
            <tr onChange={this.search}>{this.props.headers.map(function (_ignore, idx) {
                if (idx < useful)
                    return (
                        <td key={idx}><FormControl id="editInput" type="text" data-idx={idx}/></td>
                    )
                return (
                    <td key={idx}></td>
                )
            })}</tr>
        );
    }

    render() {
        return (
            this.renderTable()
        );
    }
}

export {Users};


