import React, {Component} from 'react';
import {Table, Form, FormControl, Button, ListGroup, ListGroupItem} from 'react-bootstrap';
import './ShoppingCart.css';
import {EE} from './Home';
import $ from "jquery";
import {loginID} from "./Toolbar";

const useful = 5;
const price = 2;
const quan = 3;
const sum = 4;
const cart = 5;

class ShoppingCart extends Component {
    static defaultProps = {
        headers: [
            "BookID", "Title", "Price", "Quantity", "Sum", "Delete"
        ],
        initialData: [],
    };

    constructor(props) {
        super(props);
        this.state = {
            btstyle: null,
            data: this.props.initialData,
            sortby: null,
            DESC: false,
            edit: null, //{row: index, cell: index}
            search: false,
            add: false,
            preSearchData: null,
            needles: new Array(useful),
            adds: new Array(useful),
            addvs: new Array(useful),
        };
        this.sort = this.sort.bind(this);
        this.getCart = this.getCart.bind(this);
        this.renderTable = this.renderTable.bind(this);
        this.renderSearch = this.renderSearch.bind(this);
        this.search = this.search.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
        this.showEditor = this.showEditor.bind(this);
        this.save = this.save.bind(this);
        EE.on('pushSearch', this.toggleSearch.bind(this));
        this.getCart();
    }

    getCart() {
        const _this = this;
        $.ajax({
            type: "POST",
            async: true,
            url: "http://localhost:80/CartManager",
            crossDomain: true,
            data: {
                "userid": loginID
            },
            dataType: "jsonp",
            jsonp: "jsonpCallback",
            jsonpCallback: "success_jsonpCallback",
            success: function (data) {
                _this.setState({
                    data: JSON.parse(JSON.stringify(data)),
                });
            },
            error: function () {
                alert('error');
            }
        });
    }

    showEditor(e) {
        if (e.target.cellIndex === quan)
            this.setState({
                    edit: {
                        row: parseInt(e.target.dataset.row, 10),
                        cell: e.target.cellIndex,
                    }
                }
            )
    }

    save(e) {
        e.preventDefault();
        var row = this.state.edit.row;
        var input = e.target.firstChild;
        var data0 = Array.from(this.state.data);
        data0[this.state.edit.row][this.state.edit.cell] = input.value;
        data0[this.state.edit.row][sum] = input.value * data0[this.state.edit.row][price];
        const _this = this;
        $.ajax({
            type: 'POST',
            async: false,
            url: "http://localhost:80/CartManager/Update",
            crossDomain: true,
            data: {
                "userid": loginID,
                "bookid": data0[row][0],
                "quantity": input.value,
            },
            dataType: 'text',
            success: function (data) {
                _this.setState({
                    data: data0,
                });
            },
            error: function (xhr, msg) {
                alert(msg + '\n' + xhr.responseText);
                console.log(msg + '\n' + xhr.responseText);
            }
        });
        this.setState({
            edit: null,
        })
    }

    sort(e) {
        var column = e.target.cellIndex;
        var data = Array.from(this.state.data);
        var DESC = this.state.sortby === column && !this.state.DESC;
        data.sort(function (a, b) {
            return DESC
                ? (a[column] < b[column] ? 1 : -1)
                : (a[column] > b[column] ? 1 : -1);
        });
        this.setState({
            data: data,
            sortby: column,
            DESC: DESC,
        });
    }

    toggleSearch() {
        if (this.state.search) {
            this.setState({
                data: this.state.preSearchData,
                search: false,
                preSearchData: null,
            });
        }
        else {
            this.setState({
                preSearchData: this.state.data,
                search: true,
            });
        }
    }

    search(e) {
        var needle = e.target.value.toLowerCase();
        var needles = this.state.needles;
        var data = this.state.preSearchData;
        needles[e.target.dataset.idx] = needle;
        var see = function (row) {
            return row[i].toString().toLowerCase().indexOf(needle) > -1;
        };
        for (var i = 0; i < useful; i++) {
            needle = needles[i];
            if (needle) {
                data = data.filter(see);
            }
        }
        this.setState({
            data: data,
            needles: needles,
        });
    }

    deleteItem(e) {
        var data0 = this.state.data;
        var row = e.target.getAttribute('row');
        const _this = this;
        $.ajax({
            type: 'POST',
            async: false,
            url: "http://localhost:80/CartsManager/Delete",
            crossDomain: true,
            data: {
                "userid": loginID,
                "bookid": data0[row][0],
            },
            dataType: 'text',
            success: function (data) {
                data0.splice(row, 1);
                _this.setState({
                    data: data0,
                });
            },
            error: function (xhr, msg) {
                alert(msg + '\n' + xhr.responseText);
                console.log(msg + '\n' + xhr.responseText);
            }
        });
    }

    renderTable() {
        return (
            <div id="table">
                <Table striped bordered condensed hover>
                    <thead onClick={this.sort}>
                    <tr>
                        {this.props.headers.map(function (title, idx) {
                            if (this.state.sortby === idx) {
                                title += this.state.DESC ? '\u2191' : '\u2193'
                            }
                            return (<th id="thh" key={idx}>{title}{" "}</th>);
                        }, this)}
                    </tr>
                    </thead>
                    <tbody onDoubleClick={this.showEditor}>
                    {this.renderSearch()}
                    {this.state.data.map(function (row, rowidx) {
                        return (
                            <tr key={rowidx}>{row.map(function (cell, idx) {
                                var content = cell;
                                var edit = this.state.edit;
                                if (idx === price || idx === sum) content = content / 100;
                                if (idx < useful) {
                                    if (edit && edit.row === rowidx && edit.cell === idx) {
                                        content = <Form onSubmit={this.save}>
                                            <FormControl id="editInput" type="text" defaultValue={content}/> </Form>
                                    }
                                    return (<td key={idx} data-row={rowidx}>{content}</td>);
                                }
                                if (idx === cart)
                                    return (
                                        <td key={idx} data-row={rowidx}>
                                            <Button row={rowidx} bsStyle="danger" bsSize="xsmall" onClick={this.deleteItem}>Delete</Button>
                                        </td>
                                    );
                                return (<td key={idx} data-row={rowidx}></td>);
                            }, this)}</tr>
                        );
                    }, this)}
                    </tbody>
                </Table>
                {this.renderSum()}
                <div id="sm"><Button block bsSize="large" onClick={this.submit}>Checkout</Button></div>
            </div>
        );
    }

    renderSearch() {
        if (!this.state.search) {
            return null;
        }
        return (
            <tr onChange={this.search}>{this.props.headers.map(function (_ignore, idx) {
                return (
                    <td key={idx}><FormControl id="editInput" type="text" data-idx={idx}/></td>
                )
            })}</tr>
        );
    }

    renderSum() {
        var sum = 0;
        for (var i = 0; i < this.state.data.length; i++) {
            sum += this.state.data[i][quan] * this.state.data[i][quan - 1];
        }
        sum = sum / 100;
        return (
            <ListGroup>
                <ListGroupItem bsStyle="info">Total: {sum}</ListGroupItem>
            </ListGroup>
        );
    }

    render() {
        return (
            this.renderTable()
        );
    }
}

export {ShoppingCart};


