import React, {Component} from 'react';
import "./Storehouse.css";
import {Table, Form, FormControl, FormGroup} from 'react-bootstrap';
import {EE} from './Home';
import $ from "jquery";

const price = 3;

class Storehouse extends Component {
    static defaultProps = {
        headers: [
            "Book", "Author", "Published", "Price", "Stock"
        ],
        initialData: [
        ],
    };

    constructor(props) {
        super(props);
        this.state = {
            btstyle: null,
            data: this.props.initialData,
            sortby: null,
            DESC: false,
            edit: null, //{row: index, cell: index}
            search: false,
            add: false,
            preSearchData: null,
            needles: new Array(this.props.headers.length),
            adds: new Array(this.props.headers.length),
            addvs: new Array(this.props.headers.length),
        };
        this.sort = this.sort.bind(this);
        this.showEditor = this.showEditor.bind(this);
        this.save = this.save.bind(this);
        this.renderTable = this.renderTable.bind(this);
        this.renderSearch = this.renderSearch.bind(this);
        this.renderAdd = this.renderAdd.bind(this);
        this.search = this.search.bind(this);
        this.add = this.add.bind(this);
        this.getBooks = this.getBooks.bind(this);
        EE.on('pushSearch', this.toggleSearch.bind(this));
        EE.on('add', this.toggleAdd.bind(this));
        this.getBooks();
    }

    getBooks() {
        const _this = this;
        $.ajax({
            type: "GET",
            async: true,
            url: "http://localhost:80/BookManager",
            dataType: "jsonp",
            jsonp: "jsonpCallback",
            jsonpCallback: "success_jsonpCallback",
            success: function (data) {
                _this.setState({
                    data: JSON.parse(JSON.stringify(data)),
                });
            },
            error: function () {
                alert('error');
            }
        });
    }

    sort(e) {
        var column = e.target.cellIndex;
        var data = Array.from(this.state.data);
        var DESC = this.state.sortby === column && !this.state.DESC;
        data.sort(function (a, b) {
            return DESC
                ? (a[column] < b[column] ? 1 : -1)
                : (a[column] > b[column] ? 1 : -1);
        });
        this.setState({
            data: data,
            sortby: column,
            DESC: DESC,
        });
    }

    showEditor(e) {
        this.setState({
                edit: {
                    row: parseInt(e.target.dataset.row, 10),
                    cell: e.target.cellIndex,
                }
            }
        )
    }

    save(e) {
        e.preventDefault();
        var row = this.state.edit.row;
        var input = e.target.firstChild;
        var data1 = Array.from(this.state.data);
        var data0 = Array.from(this.state.data);
        if (this.state.edit.cell === 3)
            data0[this.state.edit.row][this.state.edit.cell] = input.value * 100;
        else data0[this.state.edit.row][this.state.edit.cell] = input.value;
        const _this = this;
        //alert(data0[row][0]);
        $.ajax({
            type: 'POST',
            async: true,
            url: "http://localhost:80/BookManager/Update",
            crossDomain: true,
            data: {
                "oldtitle": data1[row][0],
                "oldauthor": data1[row][1],
                "oldpublished": data1[row][2],
                "title": data0[row][0],
                "author": data0[row][1],
                "published": data0[row][2],
                "price": data0[row][3],
                "stock": data0[row][4],
            },
            dataType: 'text',
            success: function (data) {
                _this.setState({
                    data: data0,
                });
            },
            error: function (xhr, msg) {
                alert(msg + '\n' + xhr.responseText);
                console.log(msg + '\n' + xhr.responseText);
            }
        });
        _this.setState({
            edit: null,
            data: data0,
        });
    }

    toggleSearch() {
        if (this.state.search) {
            this.setState({
                data: this.state.preSearchData,
                search: false,
                preSearchData: null,
            });
        }
        else {
            this.setState({
                preSearchData: this.state.data,
                search: true,
            });
        }
    }

    toggleAdd() {
        if (this.state.add) {
            var allempty = true;
            var allfull = true;
            var data = this.state.data;
            for (var i = 0; i < this.state.adds.length; i++) {
                if (this.state.adds[i] !== null) {
                    allempty = false;
                }
                else {
                    allfull = false;
                }
            }
            if (allempty)
                this.setState({
                    add: false,
                });
            if (allfull) {
                data.push(this.state.adds);
                this.setState({
                    add: false,
                    data: data,
                    adds: new Array(this.props.headers.length),
                });
            }
        }
        else {
            var empty = new Array(this.props.headers.length);
            for (i = 0; i < this.props.headers.length; i++) {
                empty[i] = null;
            }
            this.setState({
                adds: empty,
                add: true,
            });
        }
    }

    add(e) {
        var needle = e.target.value;
        var adds = this.state.adds;
        adds[e.target.dataset.idx] = needle;

    }

    search(e) {
        var needle = e.target.value.toLowerCase();
        var needles = this.state.needles;
        var data = this.state.preSearchData;
        needles[e.target.dataset.idx] = needle;
        var see = function (row) {
            return row[i].toString().toLowerCase().indexOf(needle) > -1;
        };
        for (var i = 0; i < needles.length; i++) {
            needle = needles[i];
            if (needle) {
                data = data.filter(see);
            }
        }
        this.setState({
            data: data,
            needles: needles,
        });
    }

    renderTable() {
        return (
            <div id="table">
                <Table striped bordered condensed hover>
                    <thead onClick={this.sort}>
                    <tr>
                        {this.props.headers.map(function (title, idx) {
                            if (this.state.sortby === idx) {
                                title += this.state.DESC ? '\u2191' : '\u2193'
                            }
                            return (<th id="thh" key={idx}>{title}{" "}</th>);
                        }, this)}
                    </tr>
                    </thead>
                    <tbody onDoubleClick={this.showEditor}>
                    {this.renderAdd()}
                    {this.renderSearch()}
                    {this.state.data.map(function (row, rowidx) {
                        return (
                            <tr key={rowidx}>{row.map(function (cell, idx) {
                                var content = cell;
                                var edit = this.state.edit;
                                const _this = this;
                                if (idx === price) content = content / 100;
                                if (edit && edit.row === rowidx && edit.cell === idx) {
                                    content = <Form onSubmit={_this.save}>
                                        <FormControl id="editInput" type="text" defaultValue={content}/> </Form>
                                }
                                return (<td key={idx} data-row={rowidx}>{content}</td>);
                            }, this)}</tr>
                        );
                    }, this)}
                    </tbody>
                </Table>
            </div>
        );
    }

    renderAdd() {
        if (!this.state.add) {
            return null;
        }
        return (
            <tr onChange={this.add}>{this.props.headers.map(function (_ignore, idx) {
                return (
                    <td key={idx}><FormGroup validationState={this.state.addvs[idx]}><FormControl id="editInput" type="text" data-idx={idx}/></FormGroup></td>
                )
            }.bind(this))}</tr>
        );
    }

    renderSearch() {
        if (!this.state.search) {
            return null;
        }
        return (
            <tr onChange={this.search}>{this.props.headers.map(function (_ignore, idx) {
                return (
                    <td key={idx}><FormControl id="editInput" type="text" data-idx={idx}/></td>
                )
            })}</tr>
        );
    }

    render() {
        return (
            this.renderTable()
        );
    }
}

export {Storehouse};


