import React, {Component} from 'react';
import './Sales.css';
import {Table, Form, FormControl, ListGroup, ListGroupItem} from 'react-bootstrap';
import {EE} from './Home';
import $ from "jquery";

const price = 3;
const sales = 5;

class Sales extends Component {
    static defaultProps = {
        headers: [
            "Book", "Author", "Published", "Price", "Sold", "Sales"
        ],
        initialData: [
        ],
    };

    constructor(props) {
        super(props);
        this.state = {
            btstyle: null,
            data: this.props.initialData,
            sortby: null,
            DESC: false,
            edit: null, //{row: index, cell: index}
            search: false,
            add: false,
            preSearchData: null,
            needles: new Array(this.props.headers.length),
            adds: new Array(this.props.headers.length),
            addvs: new Array(this.props.headers.length),
        };
        this.sort = this.sort.bind(this);
        this.getBooks = this.getBooks.bind(this);
        this.renderTable = this.renderTable.bind(this);
        this.renderSearch = this.renderSearch.bind(this);
        this.renderSales = this.renderSales.bind(this);
        this.search = this.search.bind(this);
        EE.on('pushSearch', this.toggleSearch.bind(this));
        this.getBooks();
    }

    getBooks() {
        const _this = this;
        $.ajax({
            type: "GET",
            async: true,
            url: "http://localhost:80/BookManager/Sales",
            dataType: "jsonp",
            jsonp: "jsonpCallback",
            jsonpCallback: "success_jsonpCallback",
            success: function (data) {
                _this.setState({
                    data: JSON.parse(JSON.stringify(data)),
                });
            },
            error: function () {
                alert('error');
            }
        });
    }

    sort(e) {
        var column = e.target.cellIndex;
        var data = Array.from(this.state.data);
        var DESC = this.state.sortby === column && !this.state.DESC;
        data.sort(function (a, b) {
            return DESC
                ? (a[column] < b[column] ? 1 : -1)
                : (a[column] > b[column] ? 1 : -1);
        });
        this.setState({
            data: data,
            sortby: column,
            DESC: DESC,
        });
    }

    toggleSearch() {
        if (this.state.search) {
            this.setState({
                data: this.state.preSearchData,
                search: false,
                preSearchData: null,
            });
        }
        else {
            this.setState({
                preSearchData: this.state.data,
                search: true,
            });
        }
    }

    search(e) {
        var needle = e.target.value.toLowerCase();
        var needles = this.state.needles;
        var data = this.state.preSearchData;
        needles[e.target.dataset.idx] = needle;
        var see = function (row) {
            return row[i].toString().toLowerCase().indexOf(needle) > -1;
        };
        for (var i = 0; i < needles.length; i++) {
            needle = needles[i];
            if (needle) {
                data = data.filter(see);
            }
        }
        this.setState({
            data: data,
            needles: needles,
        });
    }

    renderTable() {
        return (
            <div id="table">
                <Table striped bordered condensed hover>
                    <thead onClick={this.sort}>
                    <tr>
                        {this.props.headers.map(function (title, idx) {
                            if (this.state.sortby === idx) {
                                title += this.state.DESC ? '\u2191' : '\u2193'
                            }
                            return (<th id="thh" key={idx}>{title}{" "}</th>);
                        }, this)}
                    </tr>
                    </thead>
                    <tbody onDoubleClick={this.showEditor}>
                    {this.renderSearch()}
                    {this.state.data.map(function (row, rowidx) {
                        return (
                            <tr key={rowidx}>{row.map(function (cell, idx) {
                                var content = cell;
                                var edit = this.state.edit;
                                if (idx === price || idx === sales) content = content / 100;
                                if (edit && edit.row === rowidx && edit.cell === idx) {
                                    content = <Form onSubmit={this.save}>
                                        <FormControl id="editInput" type="text" defaultValue={content}/> </Form>
                                }
                                return (<td key={idx} data-row={rowidx}>{content}</td>);
                            }, this)}</tr>
                        );
                    }, this)}
                    </tbody>
                </Table>
                {this.renderSales()}
            </div>
        );
    }

    renderSearch() {
        if (!this.state.search) {
            return null;
        }
        return (
            <tr onChange={this.search}>{this.props.headers.map(function (_ignore, idx) {
                return (
                    <td key={idx}><FormControl id="editInput" type="text" data-idx={idx}/></td>
                )
            })}</tr>
        );
    }

    renderSales() {
        var sum = 0;
        for (var i = 0; i < this.state.data.length; i++) {
            sum += parseInt(this.state.data[i][5], 10);
        }
        sum = sum / 100;
        return (
            <ListGroup>
                <ListGroupItem bsStyle="info">Sales: {sum}</ListGroupItem>
            </ListGroup>
        );
    }

    render() {
        return (
            this.renderTable()
        );
    }
}

export {Sales};


